.postsBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
}
.posts {
    margin-top: 150px;
    margin-right: 290px;
}
.postsBlock textarea {
    width:300px;
    height: 150px;
    border-radius: 5px;
}
.postsBlock textarea:hover {
    box-shadow: 0 0 35px #3D5AFE;
}
.buttonCenter {
    display: flex;
    justify-content: center;
}

.buttonCenter button {
    width: 120px;
    text-transform: uppercase;
    height: 30px;
    color: black;
    font-size: 15px;
    font-weight: bold;
    letter-spacing: 2px;
    text-shadow: 0 4px 3px rgba(0,0,0,0.4),
    0 8px 13px rgba(0,0,0,0.1),
    0 18px 23px rgba(0,0,0,0.1);
    border-radius: 45px;
    background-color: #00aced;
    box-shadow: 0 8px 15px rgba(0,0,0,.8);
    transition: .3s;
    border:none;
    margin-top: 20px;
    outline: none;
    user-select: none;
}
.buttonCenter button:hover {
    background: #2EE59D;
    box-shadow: 0 15px 20px rgba(46,229,157,.4);
    color: white;
    transform: translateY(-7px);
}
.postInline {
    display:flex;
    justify-content: space-around;
}