.contentProfile {
    display: flex;
    justify-content: space-around;
}
.lockSpan  {
    margin-top: 20px;
}
.lockSpan span:hover {
    background-color: white;
    cursor: pointer;
    border: 1px solid deepskyblue;
    padding: 5px;
    box-shadow: 0 0 35px #3D5AFE;
}
.lockSpan input {
    padding: 5px;
    box-shadow: 0 0 35px #3D5AFE;
}
