.descriptionBlock {
    display:flex;
    flex-direction: column;
    padding: 10px;
}
.showInput{
    display:block;
    width: 220px;
    align-self: center;
    margin-top: 10px;
    text-transform: uppercase;
    color: black;
    font-size: 12px;
    font-weight: bold;
    text-shadow: 0 4px 3px rgba(0,0,0,0.4),
    0 8px 13px rgba(0,0,0,0.1),
    0 18px 23px rgba(0,0,0,0.1);
    border-radius: 45px;
    background-color: #00aced;
    box-shadow: 0 8px 15px rgba(0,0,0,.8);
    transition: .3s;
    border:none;
    outline: none;
    user-select: none;
}
.showInput:hover{
    background: #2EE59D;
    box-shadow: 0 15px 20px rgba(46,229,157,.4);
    color: white;
    transform: translateY(-7px);
}
.hideInput{
    display:none
}

.contacts {
    padding-left: 10px;
}

.mainPhoto {
    display: flex;
    align-self: center;
    max-width: 200px;
}
.noShow{
    display:none;
}

.show{
    display:block
}
.edit {
    display: flex;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 10px;
}
.edit button{
    text-transform: uppercase;
    height: 30px;
    width: 50px;
    color: black;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 2px;
    text-shadow: 0 4px 3px rgba(0,0,0,0.4),
    0 8px 13px rgba(0,0,0,0.1),
    0 18px 23px rgba(0,0,0,0.1);
    border-radius: 45px;
    background-color: #00aced;
    box-shadow: 0 8px 15px rgba(0,0,0,.8);
    transition: .3s;
    border:none;
    outline: none;
    user-select: none;
}
.center{
    display:flex;
    margin-top: 20px;
    justify-content: center;
}
.center button {
    width: 120px;
    text-transform: uppercase;
    height: 30px;
    color: black;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 2px;
    text-shadow: 0 4px 3px rgba(0,0,0,0.4),
    0 8px 13px rgba(0,0,0,0.1),
    0 18px 23px rgba(0,0,0,0.1);
    border-radius: 45px;
    background-color: #00aced;
    box-shadow: 0 8px 15px rgba(0,0,0,.8);
    transition: .3s;
    border:none;
    outline: none;
    user-select: none;
}
.center button:hover,.edit button:hover {
    background: #2EE59D;
    box-shadow: 0 15px 20px rgba(46,229,157,.4);
    color: white;
    transform: translateY(-7px);
}
